import {
	request
} from '../../request.js'
export default {
	data() {
		return {
			baseURL:this.$api,
			page:1,
			goodsList:[],
			isShow: false,
			allChecked: false,
			total:0
		};
		
	},
	onLoad(){
		this.getList();
	},
	methods: {
		//商品详情
		openDetail(goods_id){
			console.log(goods_id)
			uni.navigateTo({
				url:"/pages/common/goods-detail/goods-detail?id="+goods_id
			});
		},
		//获取列表
		getList(){
			let that = this;
			request({
				url:'index.php?s=/wap/member/apiMyCollection',
				method:"POST",
				data:{
					page:that.page,
					type:'goods',
				},
			}).then(res=>{
				that.goodsList = res.data.data.data;
				console.log(that.goodsList)
			});
		},
		// 点击编辑
		edit(){
			this.isShow = !this.isShow;
		},
		// 点击全选，所有的商品选中或全不选
		allCheck() {
			this.allChecked = !this.allChecked;
			for(let i = 0;i < this.goodsList.length;i++) {
				this.goodsList[i].active = this.allChecked;
			}
		},
		// 点击商品的选中项
		checked(index) {
			this.goodsList[index].active = !this.goodsList[index].active;
			for(let i = 0;i < this.goodsList.length;i++) {
				if(!this.goodsList[i].active) {
					this.allChecked = false;
					break;
				}
				else {
					this.allChecked = true
				}
			}
		},
		// 点击删除
		del() {
			let that = this;
			// 单一点击每个商品
			if(that.goodsList&&that.goodsList.length > 0) {
				for(let i = 0;i < that.goodsList.length;i++) {
					if(that.goodsList[i].active) {
						request({
							url:'index.php?s=/wap/Member/apiCancelFavorites',
							method:"POST",
							data:{
								fav_id:that.goodsList[i].goods_id,
								fav_type:'goods',
							},
						}).then(res=>{
							that.goodsList.splice(i,1);
						});
					}	
				}
				uni.showToast({
					title: '删除成功',
					icon: 'none'
				})
			}
			// 点击全选
			if(that.allChecked) {
				that.goodsList = '';
				that.allChecked = false
			}
		}
	}
}
